.natural-language-page {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  background: #f8fafc;
  min-height: 100vh;
}
.natural-language-page .main-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  align-items: start;
}
.natural-language-page .main-content .chat-section {
  min-height: 600px;
}
.natural-language-page .main-content .sidebar {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.natural-language-page .error-banner {
  margin-bottom: 1rem;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.75rem;
  padding: 0.75rem 1rem;
}
.natural-language-page .error-banner .error-banner-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.natural-language-page .error-banner .error-banner-content .error-banner-icon {
  flex-shrink: 0;
  color: #ef4444;
  display: flex;
  align-items: center;
}
.natural-language-page .error-banner .error-banner-content .error-banner-text {
  flex: 1;
  min-width: 0;
}
.natural-language-page .error-banner .error-banner-content .error-banner-text .error-banner-message {
  font-size: 0.875rem;
  color: #991b1b;
  line-height: 1.5;
}
.natural-language-page .error-banner .error-banner-content .error-banner-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-shrink: 0;
}
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-retry,
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-dismiss {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  padding: 0;
  background: transparent;
  border: none;
  border-radius: 0.375rem;
  color: #991b1b;
  cursor: pointer;
  transition: all 0.2s ease;
}
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-retry:hover,
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-dismiss:hover {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
}
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-retry:active,
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-dismiss:active {
  transform: scale(0.95);
}
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-retry svg,
.natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-dismiss svg {
  width: 1rem;
  height: 1rem;
}

@media (max-width: 1024px) {
  .natural-language-page .main-content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .natural-language-page .main-content .sidebar {
    order: -1;
  }
}
@media (max-width: 768px) {
  .natural-language-page {
    padding: 1rem;
  }
  .natural-language-page .main-content {
    gap: 1rem;
  }
  .natural-language-page .main-content .chat-section {
    min-height: 500px;
  }
  .natural-language-page .error-banner {
    padding: 0.625rem 0.875rem;
  }
  .natural-language-page .error-banner .error-banner-content {
    gap: 0.5rem;
  }
  .natural-language-page .error-banner .error-banner-content .error-banner-text .error-banner-message {
    font-size: 0.8rem;
  }
  .natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-retry,
  .natural-language-page .error-banner .error-banner-content .error-banner-actions .error-banner-dismiss {
    width: 1.75rem;
    height: 1.75rem;
  }
}
